<template>
  <div class="two-choice-page">
    <div class="container-fluid">
      <question
        :num="questionsNum"
        :index="questionIndex"
        :img="pageData.img"
        :name="pageData.name"
      ></question>

      <div class="row btns-box-xs">
        <div class="col-xs-6" v-for="option in pageData.options">
          <button class="btn" @click="handleNext($event, option)">
            <picture>
              <source :srcset="option.characters[1]" media="(min-width: 768px)">
              <img class="btn-img" :srcset="option.characters[0]" role="presentation">
            </picture>
            <p>{{ option.name }}</p>
          </button>
        </div>
      </div>

      <div class="row btns-box-lg">
        <div class="choices-box col-lg-6 col-lg-offset-3">
          <div class="row">
            <div
              class="col-lg-4{{ $index === 0 ? '' : ' col-lg-offset-4' }}"
              v-for="option in pageData.options"
            >
              <img
                class="option-img-lg"
                :src="option.characters[2]"
                role="presentation"
                @click="handleNext($event, option)"
              >
              <option-btn
                class="btn-lg"
                :index="$index"
                :option="option"
                :on-click="handleNext"
              >
              </option-btn>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import { questionsNum } from '../../data/pages';
import Question from '../general/Question';
import OptionBtn from '../general/OptionBtn';

export default {
  props: {
    pageData: Object,
  },
  components: {
    question: Question,
    'option-btn': OptionBtn,
  },
  data() {
    return {
      questionsNum,
    };
  },
  computed: {
    questionIndex() {
      return this.pageData.id - 1;
    },
    questionImg() {

    },
  },
  methods: {
    handleNext(event, option) {
      const path = this.pageData.path;
      this.$dispatch('handleNextPage', path, option);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";

.two-choice-page {
  min-width: 100vw;
  min-height: 100vh;
}

.container-fluid {
  margin-bottom: 2em;
}

.btns-box-xs {
  margin-top: 1.875em;
}

.btn {
  display: block;
  width: 100%;
  color: $color-font;
  justify-content: center;
  background-color: transparent;
  border: solid 0.125em $color-border;
  cursor: pointer;

  &:hover {
    border-color: $color-border-hover;
  }

  &:focus {
    outline: 0;
  }
}

.btn-img {
  width: 100%;
}

.btns-box-lg {
  display: none;
}

@media only screen and (min-width: $screen-lg) {
  .btns-box-xs {
    display: none;
  }

  .btns-box-lg {
    display: flex;
  }

  .option-img-lg {
    width: 100%;
    cursor: pointer;
  }

  .btn-lg {
    position: relative;
    border: 0;
    margin-top: 1.875em;
  }

  .btn-img-lg {
    width: 100%;
  }

  .btn-txt-lg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    color: $color-font;
  }

  .row {
    width: 100%;
  }

  .choices-box {
    min-height: 75vh;
    display: flex;
    align-items: center;
  }
}

</style>



