<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12 col-lg-3 col-lg-offset-2">
        <p class="desc-txt color-palette-desc">最多選擇3種顏色</p>
        <color-palette
          :colors="colors"
        ></color-palette>
      </div>
      <div
        class="color-slider-box col-xs-12 col-lg-5 col-lg-offset-1"
        :style="haveSelectedColors"
      >
        <p
          class="desc-txt color-slider-desc"
          :style="haveTwoSelectedColors"
        >
          {{ sliderDesc }}
        </p>
        <color-slider
          class="color-slider"
          :colors="selectedColors"
          v-ref:slider
        ></color-slider>
      </div>
    </div>
  </div>
</template>

<script>
import ColorPalette from './ColorPalette';
import ColorSlider from './ColorSlider';
import isIE from '../../../static/browserDetect';
import paths from '../../data/paths';

export default {
  props: {
    colors: Array,
  },
  data() {
    return {
      selectedColors: [],
    };
  },
  computed: {
    haveSelectedColors() {
      if (this.selectedColors.length === 0) {
        return {
          display: 'none',
        };
      }

      return {};
    },
    haveTwoSelectedColors() {
      if (this.selectedColors.length <= 1) {
        return {
          visibility: 'hidden',
        };
      }

      return {};
    },
    sliderDesc() {
      if (isIE) {
        return '拉動可以調整顏色比例';
      }

      return '用拉桿調整顏色比例';
    },
  },
  components: {
    'color-palette': ColorPalette,
    'color-slider': ColorSlider,
  },
  events: {
    handleColorClick(color) {
      let clickedColor;
      this.colors.forEach(el => {
        if (el.key === color.key) {
          clickedColor = el;
        }
      });

      if (clickedColor.isSelected) {
        // remove a color
        if (this.selectedColors.length === 1) {
          // only have one colors, so don't remove a color
          return;
        }

        // add color to selected colors list
        clickedColor.isSelected = false;
        const matchedIndex = this.selectedColors.indexOf(clickedColor);
        this.selectedColors.splice(matchedIndex, 1);
      } else {
        // add a color
        if (this.selectedColors.length === 3) {
          // selected colors have full, so pop last color
          const removeColor = this.selectedColors.pop();
          removeColor.isSelected = false;
        }

        // add a color to selected colors list
        clickedColor.isSelected = true;
        this.selectedColors.push(clickedColor);
      }
    },
  },
  beforeCompile() {
    const selectedOptions = this.$root.$children[0].selectedOptions;
    for (let i = 0; i < selectedOptions.length; i++) {
      if (selectedOptions[i].path === paths.COLORFUL_CLOTHES) {
        this.selectedColors = selectedOptions[i].option.selectedColors;

        break;
      }
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.desc-txt {
  display: none;
  font-family: $font-base;
}

@media only screen and (min-width: $screen-lg) {
  .desc-txt {
    display: block;
    font-size: 15px;
    font-weight: 300;
    color: $color-font;
    margin: 0 auto 18px;
    width: 100%;
  }

  .color-slider-desc {
    align-self: flex-start;
  }

  .color-slider-box {
    display: flex;
    flex-flow: row wrap;
  }

  .color-slider {
    width: 100%;
  }
}

</style>



