<template>
  <div class="result-page">
    <div class="container-fluid">
      <div class="box col-xs-12 col-lg-10 col-lg-offset-1">
        <div class="content">
          <div class="content-img-box col-xs-12 col-lg-6">
            <picture>
              <source :srcset="resultImg3" media="(min-width: 1200px)">
              <source :srcset="resultImg2" media="(min-width: 768px)">
              <img class="content-img" :srcset="resultImg1" role="presentation">
            </picture>
          </div>
          <div class="col-xs-12 col-lg-6">
            <div class="content-desc">
              <span class="result-title">{{ pageData.result.name }}</span>
              <p class="result-quote">{{ pageData.result.quote }}</p>
              <p class="result-subtitle">感情特質</p>
              <p class="result-desc">{{ pageData.result.traits.feeling }}</p>
              <p class="result-subtitle">穿衣特質</p>
              <p class="result-desc">{{ pageData.result.traits.clothes }}</p>
              <p class="result-desc">{{{ pageData.suggestion }}}</p>

              <div class="btns-box">
                <button
                  class="btn"
                  @click="handleReplayClick"
                  @mouseenter="handleReplayEnter"
                  @mouseleave="handleReplayLeave"
                >
                  <canvas
                    id="js-replay-btn"
                    class="btn-bg"
                    style="width: 100%"
                    width="233"
                    height="65"
                  ></canvas>
                  <div class="btn-content">
                    <picture>
                      <source srcset="../../assets/resultPage/replay@2x.png" media="(min-width: 768px)">
                      <img class="btn-img" srcset="../../assets/resultPage/replay@1x.png" role="presentation">
                    </picture>
                    <p class="btn-txt">再玩一次</p>
                  </div>
                </button>
                <button
                  class="btn"
                  @click="handleShareClick"
                  @mouseenter="handleEnter"
                  @mouseleave="handleLeave"
                >
                  <canvas
                    id="js-share-btn"
                    class="btn-bg"
                    style="width: 100%"
                    width="233"
                    height="65"
                  ></canvas>
                  <div class="btn-content">
                    <picture>
                      <source srcset="../../assets/resultPage/fb@2x.png" media="(min-width: 768px)">
                      <img class="btn-img" srcset="../../assets/resultPage/fb@1x.png" role="presentation">
                    </picture>
                    <p class="btn-txt">分享我的結果</p>
                  </div>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import imgShareBg from '../../assets/resultPage/share_button.png';
import { drawImageShareBtn } from '../../utils/draw';

export default {
  props: {
    pageData: Object,
  },
  computed: {
    resultSex() {
      return this.pageData.selectedOptions[11].option.key;
    },
    resultImg1() {
      return this.pageData.result.imgs[this.resultSex][0];
    },
    resultImg2() {
      return this.pageData.result.imgs[this.resultSex][1];
    },
    resultImg3() {
      return this.pageData.result.imgs[this.resultSex][2];
    },
  },
  methods: {
    handleReplayEnter() {
      drawImageShareBtn(this.replayImgCanvas, this.replayImgObj, true);
    },
    handleReplayLeave() {
      drawImageShareBtn(this.replayImgCanvas, this.replayImgObj, false);
    },
    handleReplayClick() {
      location.reload();
      window.ga('send', 'event', 'click', 'replay');
    },
    handleEnter() {
      drawImageShareBtn(this.imgCanvas, this.imgObj, true);
    },
    handleLeave() {
      drawImageShareBtn(this.imgCanvas, this.imgObj, false);
    },
    handleShareClick() {
      const url = `https://${location.hostname}${location.pathname}`;
      const imageUrl = `https://${location.hostname}${this.resultImg2}`;

      window.FB.ui({
        method: 'feed',
        link: url,
        picture: imageUrl,
        name: this.pageData.result.share.name,
        description: this.pageData.result.share.desc,
      }, () => {});
      window.ga('send', 'event', 'click', 'fb share');
    },
  },
  ready() {
    // replay btn
    this.replayImgObj = new Image();
    this.replayImgCanvas = document.getElementById('js-replay-btn');
    this.replayImgObj.onload = () => {
      drawImageShareBtn(this.replayImgCanvas, this.replayImgObj, false);
    };
    this.replayImgObj.src = imgShareBg;

    // share btn
    this.imgObj = new Image();
    this.imgCanvas = document.getElementById('js-share-btn');
    this.imgObj.onload = () => {
      drawImageShareBtn(this.imgCanvas, this.imgObj, false);
    };
    this.imgObj.src = imgShareBg;
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.result-page {
  min-width: 100vw;
  min-height: 100vh;
  align-content: center;
}

.container-fluid {
  margin-bottom: 2em;
}

.content-img {
  width: 100%;
  margin-top: 1.875em;
  max-width: 480px;
}

.content-desc {
  margin-top: 0.9375em;
  max-width: 480px;
}

.result-title {
  font-family: $font-title;
	font-size: 2.25em;
	color: $color-result-title;
  border-bottom: double $color-result-title;
}

.result-quote {
  font-family: $font-title;
	font-size: 0.875em;
	font-weight: 300;
	color: $color-result-title;
}

.result-subtitle {
  font-family: $font-base;
  font-size: 0.9375em;
  font-weight: 500;
  line-height: 1.5;
  color: $color-result-subtitle;
}

.result-desc {
  font-family: $font-base;
  font-size: 0.875em;
  font-weight: 300;
  line-height: 1.3;
  color: $color-font;
}

.btn {
  position: relative;
  background-color: transparent;
  border: 0;
  width: 100%;
  max-width: 200px;
  margin-top: 1.875em;
  cursor: pointer;

  &:focus {
    outline: 0;
  }
}

.btn-bg {
  width: 100%;
}

.btn-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-34%, -50%);
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}

.btn-img {
  width: 2em;
  height: 2em;
  margin-right: 0.8125em;
  display: inline-block;
}

.btn-txt {
  font-family: $font-base;
  font-size: 0.8125em;
  line-height: 1.2;
  color: #ffffff;
  margin: 10px 0 0 0;
}

.content-img-box {
  display: flex;
  justify-content: center;
}

.content-lg {
  display: none;
}

@media only screen and (min-width: $screen-lg) {
  .result-page {
    background: url(../../assets/introPage/bgcover@3x.png) no-repeat center;
    background-size: cover;
  }

  .container-fluid {
    margin: 0;
    padding: 0;
  }

  .box {
    min-height: 100vh;
    height: 100px;
    display: flex;
    align-content: center;
    justify-content: center;
  }

  .content {
    width: 1099px;
    max-width: 1100px;
    height: 553px;
    width: 100%;
    background: url("../../assets/resultPage/cover_card.svg") no-repeat center;
    background-size: cover;
    display: flex;
    flex-flow: row wrap;
    align-self: center;
  }

  .content-img-box {
    padding-left: 2em;
    justify-content: flex-end;
  }

  .content-desc {
    margin-top: 0.8em;
    padding: 2em 2em 0 2em;
  }

  .btns-box {
    text-align: right;
  }

  .btn {
    margin-top: 0;
  }
}

</style>



