<template>
  <div id="app">
    <component
      v-for="pageData in pageComponents"
      :is="pageData.template"
      :page-Data="pageData"
      class="animated page"
      :transition="transPage"
    ></component>
  </div>
</template>

<script>
import Vue from 'vue';
import { guid } from './utils/uuid';

import paths from './data/paths';
import pagesData from './data/pages';
import { computeScore, getResult } from './data/results';

import IntroPage from './components/pages/IntroPage';
import ClothesPickerPage from './components/pages/ClothesPickerPage';
import ColorPickerPage from './components/pages/ColorPickerPage';
import MultipleChoicePage from './components/pages/MultipleChoicePage';
import TwoChoicePage from './components/pages/TwoChoicePage';
import ResultPage from './components/pages/ResultPage';

const trans = {
  PUST_PAGE: 'pustPage',
  POP_PAGE: 'popPage',
};

Vue.transition(trans.PUST_PAGE, {
  enterClass: 'fadeInRight',
  leaveClass: 'fadeOutLeft',
});

Vue.transition(trans.POP_PAGE, {
  enterClass: 'fadeInLeft',
  leaveClass: 'fadeOutRight',
});

export default {
  components: {
    'intro-page': IntroPage,
    'clothes-picker-page': ClothesPickerPage,
    'color-picker-page': ColorPickerPage,
    'multiple-choice-page': MultipleChoicePage,
    'two-choice-page': TwoChoicePage,
    'result-page': ResultPage,
  },
  data() {
    return {
      pageComponents: [],
      pagesData,
      curPageIndex: 0,
      transPage: trans.PUST_PAGE,
      selectedOptions: [],
      startTime: null,
      endTime: null,
    };
  },
  events: {
    handleNextPage(path, option) {
      // setup transition
      this.transPage = trans.PUST_PAGE;

      // save selected option
      let selectedIndex = -1;
      for (let i = 0; i < this.selectedOptions.length; i++) {
        if (this.selectedOptions[i].path === path) {
          selectedIndex = i;
          break;
        }
      }

      if (selectedIndex === -1) {
        this.selectedOptions.push({
          path,
          option,
        });
      } else {
        const deletectCount = this.selectedOptions.length - selectedIndex;
        this.selectedOptions.splice(selectedIndex, deletectCount, {
          path,
          option,
        });
      }

      // compute score
      let result = {};
      if (this.curPageIndex === 0) {
        this.startTime = Date.now();
      } else if (this.curPageIndex === this.pagesData.length - 2) {
        result = getResult(this.selectedOptions);
        const pageData = this.pagesData[this.pagesData.length - 1];
        pageData.result = result;
        pageData.selectedOptions = this.selectedOptions;

        // record data
        const period = Date.now() - this.startTime;
        const score = computeScore(this.selectedOptions);
        const answers = [];
        this.selectedOptions.forEach(item => {
          switch (item.path) {
            case paths.MANY_CLOTHES:
            case paths.SELDOM:
              answers.push(item.option.value);
              break;
            case paths.COLORFUL_CLOTHES:
              answers.push(
                item.option.selectedColors.map(el => ({
                  key: el.key,
                  value: el.value,
                }))
              );
              break;
            case paths.SAME_CLOTHES:
            case paths.FREQUENTLY:
            case paths.PICK:
            case paths.SPEND_TIME:
            case paths.THOUGHT:
            case paths.HANDLE:
            case paths.OTHERS_OR_ONSELF:
            case paths.SEX:
            case paths.AGE:
              answers.push(item.option.id);
              break;
            // skip
            case paths.INTRO:
            default:
          }
        });
        const recordData = {
          period,
          score,
          answers,
        };

        const body = new FormData();
        body.append('data', JSON.stringify(recordData));

        this.$http.post(
          'https://aqueous-castle-38966.herokuapp.com/data',
          body
        ).then(() => {});
      }

      // compute data of page components
      this.curPageIndex++;
      this.pageComponents.pop();
      const page = this.pagesData[this.curPageIndex];
      this.pageComponents.push(page);

      // push state to history
      history.state.currentIndex++;
      history.state.paths.push(page.path);
      history.pushState(history.state, '', `#${page.path}`);

      // GA events
      switch (path) {
        case paths.INTRO:
          window.ga('send', 'event', 'question', path, option.name);
          break;
        case paths.MANY_CLOTHES:
        case paths.SELDOM:
          window.ga('send', 'event', 'question', `${path}2`, option.name, option.value);
          if (option.value < 30) {
            window.ga('send', 'event', 'question', path, '<30');
          } else if (option.value >= 30 && option.value < 60) {
            window.ga('send', 'event', 'question', path, '30-59');
          } else if (option.value >= 60 && option.value < 90) {
            window.ga('send', 'event', 'question', path, '60-89');
          } else {
            window.ga('send', 'event', 'question', path, '>90');
          }
          break;
        case paths.COLORFUL_CLOTHES:
          option.selectedColors.forEach(el => {
            window.ga('send', 'event', 'question', path, el.key, el.value);
          });
          break;
        case paths.SAME_CLOTHES:
        case paths.FREQUENTLY:
        case paths.PICK:
        case paths.SPEND_TIME:
        case paths.THOUGHT:
        case paths.HANDLE:
        case paths.OTHERS_OR_ONSELF:
        case paths.SEX:
          window.ga('send', 'event', 'question', path, option.name);
          break;
        case paths.AGE:
          window.ga('send', 'event', 'question', path, option.name);
          window.ga('send', 'event', 'result', result.name);
          break;
        default:
      }
    },
  },
  created() {
    this.$watch('pageComponents', (el) => {
      // record GA
      window.ga('send', {
        hitType: 'pageview',
        page: el[0].path,
      });

      window.scrollTo(0, 0);
      window.picturefill();
    });

    // init data
    const page = this.pagesData[this.curPageIndex];

    // reset history state
    this.uniqueId = guid();
    window.history.replaceState({
      uniqueId: this.uniqueId,
      currentIndex: this.curPageIndex,
      paths: [page.path],
    }, '', `#${page.path}`);

    // init page components
    this.pageComponents.push(page);
  },
  ready() {
    window.onpopstate = (event) => {
      if (!event.state) {
        return;
      }

      if (event.state.uniqueId !== this.uniqueId) {
        location.reload();
        return;
      }

      // back
      if (event.state.currentIndex < this.curPageIndex) {
        // setup transition
        this.transPage = trans.POP_PAGE;

        // compute data of page components
        this.curPageIndex--;
        this.pageComponents.pop();
        const page = this.pagesData[this.curPageIndex];
        this.pageComponents.push(page);
      } else {
        // forward
        // setup transition
        this.transPage = trans.PUST_PAGE;

        // compute data of page components
        this.curPageIndex++;
        this.pageComponents.pop();
        const page = this.pagesData[this.curPageIndex];
        this.pageComponents.push(page);
      }
    };
  },
};
</script>

