<template>
  <button
    class="btn"
    @click="onClick($event, option)"
    @mouseenter="handleEnter"
    @mouseleave="handleLeave"
  >
    <canvas
      :id="calcOptionId"
      class="btn-img"
      style="width: 100%"
      :width="option.size.width"
      :height="option.size.height"
    ></canvas>
    <p class="btn-txt">{{ option.name }}</p>
  </button>
</template>

<script>
import { drawImageInvertH } from '../../utils/draw';

export default {
  props: {
    index: Number,
    option: Object,
    onClick: Function,
  },
  data() {
    return {
      imgCanvas: null,
    };
  },
  computed: {
    calcOptionId() {
      return `option-${this.option.id}`;
    },
  },
  methods: {
    handleEnter() {
      drawImageInvertH(this.imgCanvas, this.imgObj, true);
    },
    handleLeave() {
      drawImageInvertH(this.imgCanvas, this.imgObj, false);
    },
  },
  ready() {
    this.imgObj = new Image();
    this.imgCanvas = document.getElementById(`option-${this.option.id}`);
    this.imgObj.onload = () => {
      drawImageInvertH(this.imgCanvas, this.imgObj, false);
    };
    this.imgObj.src = this.option.img;
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.btn {
  display: block;
  position: relative;
  width: 100%;
  max-width: 442px;
  height: 46px;
  color: $color-font;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  border: solid 0.125em $color-border;

  &:focus {
    outline: 0;
  }
}

.btn-img {
  width: 100%;
  display: none;
}

.btn-txt {
  font-size: 0.8125em;
  font-weight: 300;
  width: 100%;
  margin: 0;
  text-align: center;
  font-family: $font-base;
}

@media only screen and (min-width: $screen-lg) {
  .btn {
    border: 0;
    height: auto;

    &:hover {
      .btn-txt {
        color: white;
      }
    }
  }

  .btn-img {
    display: block;
  }

  .btn-txt {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
  }
}

</style>



