<template>
  <div>
    <div
      class="color-palette-box"
      v-for="color in colors"
    >
      <div
        class="color-palette"
        :class="{ 'color-palette-selected': color.isSelected }"
        @click="handleClick(color)">
        <img
          class="color"
          :class="{ 'color-selected': color.isSelected }"
          :src="color.src">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    colors: Array,
  },
  methods: {
    handleClick(color) {
      this.$parent.$dispatch('handleColorClick', color);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

@import "../../utils/screen.scss";

.color-palette-box {
  display: inline-block;
  width: 25%;
  margin-bottom: 1.5em;
  cursor: pointer;
}

.color {
  width: 100%;
  opacity: 0.2;
}

.color-selected {
  opacity: 1;
}

@media only screen and (min-width: $screen-lg) {
  .color-palette {
    &:hover {
      .color {
        opacity: 1;
      }
    }
  }
}

</style>



