<template>
  <div class="multiple-choice-page">
    <div class="container-fluid">
      <question
        :num="questionsNum"
        :index="questionIndex"
        :img="pageData.imgs[0]"
        :name="pageData.name"
      ></question>

      <div class="choices-box row">
        <div class="col-lg-5 col-lg-offset-1">
          <picture>
            <source :srcset="pageData.imgs[2]" media="(min-width: 1200px)">
            <source :srcset="pageData.imgs[1]" media="(min-width: 768px)">
            <img class="question-img" :srcset="pageData.imgs[0]" role="presentation">
          </picture>
        </div>

        <div class="col-xs-12 col-lg-5">
          <div
            class="option-btn-box"
            v-if="isDisplayType1"
            v-for="option in pageData.options"
          >
            <option-btn
              :index="$index"
              :option="option"
              :on-click="handleNext"
            ></option-btn>
          </div>

          <div v-if="isDisplayType2" class="row">
            <div
              class="option-btn-box col-lg-6"
              v-for="option in pageData.options"
            >
              <option-btn
                :index="$index"
                :option="option"
                :on-click="handleNext"
              ></option-btn>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { questionsNum } from '../../data/pages';
import Question from '../general/Question';
import OptionBtn from '../general/OptionBtn';

export default {
  props: {
    pageData: Object,
  },
  components: {
    question: Question,
    'option-btn': OptionBtn,
  },
  data() {
    return {
      questionsNum,
    };
  },
  computed: {
    questionIndex() {
      return this.pageData.id - 1;
    },
    isDisplayType1() {
      const length = this.pageData.options.length;
      return length <= 4
        && this.pageData.id !== 13;
    },
    isDisplayType2() {
      const length = this.pageData.options.length;
      return length > 4
        || this.pageData.id === 13;
    },
  },
  methods: {
    handleNext(event, option) {
      const path = this.pageData.path;
      this.$dispatch('handleNextPage', path, option);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";

.multiple-choice-page {
  min-width: 100vw;
  min-height: 100vh;
}

.container-fluid {
  margin-bottom: 2em;
}

.question-img {
  display: none;
}

.option-btn-box {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 0.625em;

  &:first-child {
    margin-top: 1.25em;
  }
}

@media only screen and (min-width: $screen-lg) {
  .choices-box {
    min-height: 75vh;
    display: flex;
    align-items: center;
  }

  .question-img {
    width: 100%;
    display: block;
  }
}

</style>



