<template>
  <div class="color-picker-page">
    <div class="container-fluid">
      <question
        :num="questionsNum"
        :index="questionIndex"
        :name="pageData.name"
      ></question>

      <div class="row">
        <div class="color-picker-box col-xs-12">
          <color-picker
            :colors="pageData.colors"
            v-ref:picker
          ></color-picker>
        </div>
      </div>

      <div class="row">
        <div class="next-btn-box col-xs-12 col-lg-4 col-lg-offset-4">
          <next-btn
            class="btn"
            :option="pageData.next"
            :on-click="handleNext"
          ></next-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { questionsNum } from '../../data/pages';
import Question from '../general/Question';
import ColorPicker from '../general/ColorPicker';
import NextBtn from '../general/NextBtn';

export default {
  props: {
    pageData: Object,
  },
  components: {
    question: Question,
    'color-picker': ColorPicker,
    'next-btn': NextBtn,
  },
  data() {
    return {
      questionsNum,
    };
  },
  computed: {
    questionIndex() {
      return this.pageData.id - 1;
    },
  },
  methods: {
    handleNext(event, option) {
      const path = this.pageData.path;
      const picker = this.$refs.picker;
      const slider = picker.$refs.slider;
      const selectedColors = picker.selectedColors.map((current, index, array) => {
        const el = current;

        if (array.length === 1) {
          el.value = slider.left;
        } else if (array.length === 2) {
          if (index === 0) {
            el.value = slider.left;
          } else {
            el.value = 100 - slider.left;
          }
        } else {
          if (index === 0) {
            el.value = slider.left;
          } else if (index === 1) {
            el.value = slider.right - slider.left;
          } else {
            el.value = 100 - slider.right;
          }
        }
        return current;
      });

      const newOption = Object.assign({}, option, {
        selectedColors,
      });
      this.$dispatch('handleNextPage', path, newOption);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";

.color-picker-page {
  min-width: 100vw;
  min-height: 100vh;
}

.container-fluid {
  margin-bottom: 2em;
}

.color-picker-box {
  margin-top: 1.25em;
}

.next-btn-box {
  display: flex;
  justify-content: center;
  margin-top: 24px;
}

.btn {
  max-width: 264px;
  width: 100%;
}

@media only screen and (min-width: $screen-lg) {
  .color-picker-box {
    min-height: 60vh;
    display: flex;
    align-items: center;
  }
}
</style>



