/**
 * Converts an RGB color value to HSV. Conversion formula
 * adapted from http://en.wikipedia.org/wiki/HSV_color_space.
 * Assumes r, g, and b are contained in the set [0, 255] and
 * returns h, s, and v in the set [0, 1].
 *
 * @param   Number  r       The red color value
 * @param   Number  g       The green color value
 * @param   Number  b       The blue color value
 * @return  Array           The HSV representation
 */
function rgbToHsv(r, g, b) {
  const newR = r / 255;
  const newG = g / 255;
  const newB = b / 255;

  const max = Math.max(newR, newG, newB);
  const min = Math.min(newR, newG, newB);

  let h;

  const d = max - min;
  const s = max === 0 ? 0 : (d / max);

  const v = max;

  if (max === min) {
    h = 0; // achromatic
  } else {
    switch (max) {
      case newR:
        h = (newG - newB) / d + (newG < newB ? 6 : 0);
        break;
      case newG:
        h = (newB - newR) / d + 2;
        break;
      case newB:
        h = (newR - newG) / d + 4;
        break;
      default:
    }
    h /= 6;
  }

  return [h, s, v];
}

/**
 * Converts an HSV color value to RGB. Conversion formula
 * adapted from http://en.wikipedia.org/wiki/HSV_color_space.
 * Assumes h, s, and v are contained in the set [0, 1] and
 * returns r, g, and b in the set [0, 255].
 *
 * @param   Number  h       The hue
 * @param   Number  s       The saturation
 * @param   Number  v       The value
 * @return  Array           The RGB representation
 */
function hsvToRgb(h, s, v) {
  let r;
  let g;
  let b;

  const i = Math.floor(h * 6);
  const f = h * 6 - i;
  const p = v * (1 - s);
  const q = v * (1 - f * s);
  const t = v * (1 - (1 - f) * s);

  switch (i % 6) {
    case 0:
      r = v;
      g = t;
      b = p;
      break;
    case 1:
      r = q;
      g = v;
      b = p;
      break;
    case 2:
      r = p;
      g = v;
      b = t;
      break;
    case 3:
      r = p;
      g = q;
      b = v;
      break;
    case 4:
      r = t;
      g = p;
      b = v;
      break;
    case 5:
      r = v;
      g = p;
      b = q;
      break;
    default:
  }

  return [r * 255, g * 255, b * 255];
}

export function drawImage(canvas, imageObj, percentage = 1) {
  const context = canvas.getContext('2d');
  const x = 0;
  const y = 0;

  context.drawImage(imageObj, x, y);

  const imageWidth = imageObj.width;
  const imageHeight = imageObj.height;

  const imageData = context.getImageData(x, y, imageWidth, imageHeight);
  const data = imageData.data;
  const length = data.length;

  for (let i = 0; i < length; i += 4) {
    const width = i % (imageWidth * 4);
    const percentageWidth = imageWidth * 4 * percentage;

    if (width >= percentageWidth) {
      const brightness = 0.34 * data[i] + 0.5 * data[i + 1] + 0.16 * data[i + 2];
      // red
      data[i] = brightness;
      // green
      data[i + 1] = brightness;
      // blue
      data[i + 2] = brightness;
    }
  }

  // overwrite original image
  context.putImageData(imageData, x, y);
}

export function drawImage2(canvas, imageObj, percentage = 1) {
  const context = canvas.getContext('2d');
  const x = 0;
  const y = 0;

  context.drawImage(imageObj, x, y);

  const imageWidth = imageObj.width;
  const imageHeight = imageObj.height;

  const imageData = context.getImageData(x, y, imageWidth, imageHeight);
  const data = imageData.data;
  const length = data.length;

  for (let i = 0; i < length; i += 4) {
    const width = i % (imageWidth * 4);
    const percentageWidth = imageWidth * 4 * percentage;

    if (width >= percentageWidth) {
      // alpha
      data[i + 3] = 51;
    } else {
      // alpha
      data[i + 3] = 255;
    }
  }

  // overwrite original image
  context.putImageData(imageData, x, y);
}

export function drawImageInvertH(canvas, imageObj, invert) {
  const context = canvas.getContext('2d');
  const x = 0;
  const y = 0;
  context.clearRect(0, 0, canvas.width, canvas.height);

  context.drawImage(imageObj, x, y);

  if (!invert) {
    return;
  }

  const imageWidth = imageObj.width;
  const imageHeight = imageObj.height;

  const imageData = context.getImageData(x, y, imageWidth, imageHeight);
  const data = imageData.data;
  const length = data.length;

  for (let i = 0; i < length; i += 4) {
    const hsv = rgbToHsv(data[i], data[i + 1], data[i + 2]);
    hsv[0] = 0.07778;
    // hsv[1] = 1;
    hsv[2] += 0.1;

    const rgb = hsvToRgb(hsv[0], hsv[1], hsv[2]);
    // red
    data[i] = rgb[0];
    // green
    data[i + 1] = rgb[1];
    // blue
    data[i + 2] = rgb[2];
  }

  // overwrite original image
  context.putImageData(imageData, x, y);
}

export function drawImageShareBtn(canvas, imageObj, invert) {
  const context = canvas.getContext('2d');
  const x = 0;
  const y = 0;
  context.clearRect(0, 0, canvas.width, canvas.height);

  context.drawImage(imageObj, x, y);

  if (!invert) {
    return;
  }

  const imageWidth = imageObj.width;
  const imageHeight = imageObj.height;

  const imageData = context.getImageData(x, y, imageWidth, imageHeight);
  const data = imageData.data;
  const length = data.length;

  for (let i = 0; i < length; i += 4) {
    const hsv = rgbToHsv(data[i], data[i + 1], data[i + 2]);
    hsv[0] = 0.55;
    hsv[1] += 0.07;
    hsv[2] -= 0.07;

    const rgb = hsvToRgb(hsv[0], hsv[1], hsv[2]);
    // red
    data[i] = rgb[0];
    // green
    data[i + 1] = rgb[1];
    // blue
    data[i + 2] = rgb[2];
  }

  // overwrite original image
  context.putImageData(imageData, x, y);
}



