  <template>
  <div class="intro-page">
    <div class="row">
      <div class="desc-box col-xs-12">
        <picture>
          <source srcset="../../assets/introPage/bgcard@1x.svg" media="(min-width: 1200px)">
          <img class="desc-bg" srcset="../../assets/introPage/bgcard@1x.svg" role="presentation">
        </picture>

        <div class="desc-content">
          <div class="desc-img-box">
            <img class="desc-img" srcset="../../assets/introPage/title.svg" role="presentation">
          </div>
          <p class="desc-txt">{{{ pageData.name }}}</p>
          <next-btn
            class="desc-btn"
            :option="pageData.next"
            :on-click="handleNext"
          ></next-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NextBtn from '../general/NextBtn';

export default {
  props: {
    pageData: Object,
  },
  components: {
    'next-btn': NextBtn,
  },
  methods: {
    handleNext(event, option) {
      const path = this.pageData.path;

      this.$dispatch('handleNextPage', path, option);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.intro-page {
  min-width: 100vw;
  width: 100px; // for stupid IE
  min-height: 100vh;
  height: 100px; // for stupid IE
  background: url(../../assets/introPage/bgcover@1x.png) no-repeat center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;

  @media only screen and (min-width: $screen-sm) {
    background-image: url(../../assets/introPage/bgcover@2x.png);
  }
}

.row {
  width: 100%;
  justify-content: center;
  margin-bottom: 2em;
}

.desc-box {
  position: relative;
  width: 100%;
  padding: 0;
  max-width: 500px;
}

.desc-bg {
  width: 100%;
}

.desc-content {
  position: absolute;
  top: 0;
  left: 0.5em;
  right: 0.5em;
  bottom: 0;
}

.desc-img-box {
  width: 75%;
  margin: auto;
}

.desc-img {
  width: 100%;
  margin-top: 3em;
}

.desc-txt {
  color: $color-font;
  margin: 0 2.75em;
  font-family: $font-base;
  text-align: center;
	font-size: 1em;
	font-weight: 300;
  line-height: 1.2;
}

.desc-btn {
  width: 60%;
  margin: 0.6875em auto 1.5em;
}

@media only screen and (min-width: 414px) {
  .desc-content {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
}

@media only screen and (min-width: $screen-sm) {
  .desc-img-box {
    margin-top: 1.2em;
  }

  .desc-txt {
  	font-size: 1.3125em;
    margin-top: 0.7em;
    margin-bottom: 1em;
    line-height: 1.5;
  }

  .desc-btn {
    margin-bottom: 2.5em;
  }
}

@media only screen and (min-width: $screen-lg) {
  .intro-page {
    background-image: url(../../assets/introPage/bgcover@3x.png);
  }

  .desc-btn {
    margin-bottom: 3em;
  }
}

</style>



