<template>
  <div class="box">
    <div class="number-box" :style="numberBoxStyle">
      <span class="number">
        <span class="number-txt">{{calcValue}}</span>
        <img class="number-line" src="../../assets/general/line.png" role="presentation">
      </span>
      <span :class="unitClass">{{computUnit}}</span>
    </div>
    <div class="slider-box">
      <div class="slider">
        <div class="range" :style="rangeStyle">
          <div
            v-for="n in 11"
            class="degree"
            :style="{ left: `${n * 10}%` }"
          ></div>
        </div>

        <div class="inverse-left" :style="inverseLeftStyle"></div>

        <span class="thumb" :style="thumbStyle"></span>
      </div>

      <input
        class="input-range"
        type="range"
        tabindex="0"
        value="{{* value}}"
        max="100"
        min="0"
        step="1"
        v-model="value"
        @input="handleInput"
      />

      <input
        class="input-range input-range-track"
        type="range"
        tabindex="0"
        value="{{* value}}"
        max="100"
        min="0"
        step="1"
        v-model="value"
        @input="handleInput"
      />
    </div>

    <canvas id="clothes-canvas" style="width: 100%"></canvas>
  </div>
</template>

<script>

import { drawImage, drawImage2 } from '../../utils/draw';

export default {
  props: {
    id: Number,
    imgs: Array,
    unit: String,
  },
  data() {
    return {
      value: 50,
      imgObj: new Image(),
    };
  },
  computed: {
    calcValue() {
      return this.unitType === 'percentage' ? `${this.value}%` : this.value;
    },
    percentageValue() {
      return this.value / 100;
    },
    numberBoxStyle() {
      if (this.value === 100) {
        return {
          maxWidth: '170px',
        };
      }

      return {};
    },
    unitType() {
      if (this.unit === '％') {
        return 'percentage';
      }

      return 'amount';
    },
    unitClass() {
      if (this.unit === '％') {
        return 'hidden';
      }

      return 'unit';
    },
    computUnit() {
      if (this.value === 100
        && this.unit === '件') {
        return `${this.unit}以上`;
      }

      return this.unit;
    },
    inverseLeftStyle() {
      return {
        width: `${this.value}%`,
      };
    },
    thumbStyle() {
      return {
        left: `${this.value}%`,
      };
    },
  },
  methods: {
    handleInput() {
      if (this.id === 2) {
        drawImage(this.imgCanvas, this.imgObj, this.percentageValue);
      } else if (this.id === 9) {
        drawImage2(this.imgCanvas, this.imgObj, this.percentageValue);
      }
    },
  },
  ready() {
    this.imgObj = new Image();
    this.imgCanvas = document.getElementById('clothes-canvas');
    this.imgObj.onload = () => {
      if (this.id === 2) {
        drawImage(this.imgCanvas, this.imgObj, this.percentageValue);
      } else if (this.id === 9) {
        drawImage2(this.imgCanvas, this.imgObj, this.percentageValue);
      }
    };

    if (window.innerWidth > 1200) {
      this.imgObj.src = this.imgs[2].src;
      this.imgCanvas.setAttribute('width', this.imgs[2].width);
      this.imgCanvas.setAttribute('height', this.imgs[2].height);
    } else if (window.innerWidth > 768) {
      this.imgObj.src = this.imgs[1].src;
      this.imgCanvas.setAttribute('width', this.imgs[1].width);
      this.imgCanvas.setAttribute('height', this.imgs[1].height);
    } else {
      this.imgObj.src = this.imgs[0].src;
      this.imgCanvas.setAttribute('width', this.imgs[0].width);
      this.imgCanvas.setAttribute('height', this.imgs[0].height);
    }
  },
};

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/font.scss";
@import "../../utils/color.scss";
$color-number: #7fa1ae;

.number-box {
  position: relative;
  margin: 0 auto 66px;
  max-width: 150px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: flex-end;
}

.number {
  font-family: $font-writing;
  font-size: 67px;
  color: $color-number;
  line-height: 0.6em;
  display: flex;
  flex-flow: column wrap;
  text-align: center;
}

.number-txt {
  min-width: 100px;
  display: inline-block;
}

.unit {
  position: absolute;
  right: 0;
  bottom: 0.8125em;
  font-family: $font-base;
  font-size: 15px;
  font-weight: 300;
  color: $color-font;
}

.hidden {
  display: none;
}

$default-color-track: #CCC;
$color-thumb: #e2e666;
$color-degree: #e2e666;

$thumb-side: 28px;
$slider-radius: 4px;

$track-height: 6px;
$track-margin: 0;

$thumb-track-gutter: 6px;

$degree-margin-top: 9px;
$degree-height: 14px;
$degree-width: 2px;

$slider-margin-bottom: 31px;

.slider-box {
  position: relative;
  height: $track-height;
  text-align: left;
  margin-top: $thumb-side + $track-height;
  margin-bottom: $slider-margin-bottom;
}

.slider {
  position: absolute;
  left: $thumb-side / 2;
  right: $thumb-side / 2;
  height: $track-height;
}

.thumb {
  position: absolute;
  top: -($track-height + $thumb-side + $thumb-track-gutter);
  z-index: 2;
  height: $thumb-side;
  width: $thumb-side;
  text-align: left;
  margin-left: -$thumb-side/2;
  cursor: pointer;
  background-color: $color-thumb;
  outline: none;
  border-radius: $thumb-side/2 $thumb-side/2 0 $thumb-side/2;
  transform:rotate(45deg);
}

.inverse-left {
  position: absolute;
  left: 0;
  width: 50%;
  height: $track-height;
  background-color: $color-thumb;
  margin: 0 $track-margin;
  border-radius: $slider-radius;
}

.range {
  position: absolute;
  left: 0;
  right: 0;
  height: $track-height;
  background-color: $default-color-track;
  border-radius: $slider-radius;
}

.degree {
  position: absolute;
  top: $track-height;
  width: $degree-width;
  height: $degree-height;
  background-color: $color-degree;
  margin-top: $degree-margin-top;
}

.input-range {
  position: absolute;
  -webkit-appearance: none;
  z-index: 3;
  height: $thumb-side * 2;
  top: -($track-height + $thumb-side * 2);
  width: 100%;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  cursor: pointer;
}

.input-range::-ms-track {
  -webkit-appearance: none;
  background: transparent;
  color: transparent;
}

.input-range::-moz-range-track {
  -moz-appearance: none;
  background: transparent;
  color: transparent;
}

.input-range:focus::-webkit-slider-runnable-track {
  background: transparent;
  border: transparent;
}

.input-range:focus {
  outline: none;
}

.input-range::-ms-thumb {
  pointer-events: all;
  width: $thumb-side * 2;
  height: $thumb-side * 2;
  border-radius: 0;
  border: 0 none;
  cursor: pointer;
}

.input-range::-moz-range-thumb {
  pointer-events: all;
  width: $thumb-side * 2;
  height: $thumb-side * 2;
  border-radius: 0;
  border: 0 none;
  cursor: pointer;
}

.input-range::-webkit-slider-thumb {
  pointer-events: all;
  width: $thumb-side * 2;
  height: $thumb-side * 2;
  border-radius: 0;
  border: 0 none;
  -webkit-appearance: none;
  cursor: pointer;
}

.input-range::-ms-fill-lower {
  background: transparent;
  border: 0 none;
}

.input-range::-ms-fill-upper {
  background: transparent;
  border: 0 none;
}

.input-range::-ms-tooltip {
  display: none;
}

.input-range-track {
  top: -($track-height / 2);
  height: $track-height * 2;
  z-index: 3;
  cursor: pointer;
  pointer-events: auto;
}

#clothes-canvas {
  margin-top: 13.3px;
}

</style>



