<template>
  <div class="row">
    <div class="question-index col-xs-12 col-lg-1 col-lg-offset-1">
      <img class="question-index-bg" src="../../assets/general/number.png" role="presentation" />
      <div class="question-index-num">{{ parsedIndex }} / {{ num }}</div>
    </div>

    <div
      v-if="hasImg"
      class="question-img-box col-xs-12"
    >
      <picture>
        <source :srcset="img" media="(max-width: 1200px)">
        <img class="question-img-xs" role="presentation">
      </picture>
    </div>

    <div class="question-content col-xs-12 col-lg-10">
      <p class="question-txt">{{{ name }}}</p>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    num: Number,
    index: Number,
    img: String,
    name: String,
  },
  computed: {
    parsedIndex() {
      return this.index < 10 ? `0${this.index}` : this.index;
    },
    hasImg() {
      return this.img && this.img.length > 0;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.clothes-picker-page {
  min-width: 100vw;
  min-height: 100vh;
}

.question-index {
  margin-top: 1.25em;
  font-size: 1.4em;
  color: $color-question-index;
  text-align: center;
  font-family: $font-writing;
  text-align: center;
}

.question-index-bg {
  display: none;
}

.question-img-xs {
  width: 100%;
  margin-top: 0.625em;
}

.question-txt {
  text-align: center;
  font-family: $font-base;
  color: $color-font;
  line-height: 1.375em;
  margin: 10px 0 0 0;
}

@media only screen and (min-width: $screen-lg) {
  .question-index {
    position: relative;
    color: $color-question-index-lg;
    max-width: 120px;
  }

  .question-index-bg {
    display: block;
    width: 100%;
  }

  .question-index-num {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .question-img-box {
    display: none;
  }

  .question-content {
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }

  .question-txt {
    text-align: left;
  }
}

</style>



