<template>
  <button
    class="btn"
    @click="onClick($event, option)"
    @mouseenter="handleEnter"
    @mouseleave="handleLeave"
  >
    <canvas class="btn-img" style="width: 100%" width="264" height="57"></canvas>
    <p class="btn-txt">{{ option.name }}</p>
  </button>
</template>

<script>
import { drawImageInvertH } from '../../utils/draw';
import btnSrc from '../../assets/general/button.png';

export default {
  props: {
    option: Object,
    onClick: Function,
  },
  data() {
    return {
      imgObj: null,
      imgCanvas: null,
    };
  },
  methods: {
    handleEnter() {
      drawImageInvertH(this.imgCanvas, this.imgObj, true);
    },
    handleLeave() {
      drawImageInvertH(this.imgCanvas, this.imgObj, false);
    },
  },
  ready() {
    this.imgObj = new Image();
    this.imgCanvas = document.getElementsByClassName('btn-img')[0];
    this.imgObj.onload = () => {
      drawImageInvertH(this.imgCanvas, this.imgObj, false);
    };
    this.imgObj.src = btnSrc;
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";
@import "../../utils/font.scss";

.btn {
  display: block;
  position: relative;
  max-width: 442px;
  height: 46px;
  color: $color-font;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  border: solid 0.125em $color-border;

  &:hover {
    border-color: $color-border-hover;
  }

  &:focus {
    outline: 0;
  }
}

.btn-img {
  width: 100%;
  display: none;
}

.btn-txt {
  font-size: 1em;
  font-weight: 300;
  width: 100%;
  margin: 0;
  text-align: center;
  font-family: $font-base;
}

@media only screen and (min-width: $screen-lg) {
  .btn {
    border: 0;
    height: auto;

    &:hover {
      .btn-txt {
        color: white;
      }
    }
  }

  .btn-img {
    display: block;
  }

  .btn-txt {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
  }
}

</style>



