<template>
  <div class="clothes-picker-page">
    <div class="container-fluid">
      <question
        :num="questionsNum"
        :index="questionIndex"
        :name="pageData.name"
      ></question>

      <div class="row">
        <div class="clothes-slider-box col-xs-12 col-lg-10 col-lg-offset-1">
          <clothes-slider
            :id="pageData.id"
            :imgs="pageData.imgs"
            :unit="pageData.unit"
            v-ref:slider
          ></clothes-slider>
        </div>
      </div>

      <div class="row">
        <div class="next-btn-box col-xs-12 col-lg-4 col-lg-offset-4">
          <next-btn
            class="btn"
            :option="pageData.next"
            :on-click="handleNext"
          ></next-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { questionsNum } from '../../data/pages';
import Question from '../general/Question';
import ClothesSlider from '../general/ClothesSlider';
import NextBtn from '../general/NextBtn';

export default {
  props: {
    pageData: Object,
  },
  components: {
    question: Question,
    'clothes-slider': ClothesSlider,
    'next-btn': NextBtn,
  },
  data() {
    return {
      questionsNum,
    };
  },
  computed: {
    questionIndex() {
      return this.pageData.id - 1;
    },
  },
  methods: {
    handleNext(event, option) {
      const path = this.pageData.path;
      const newOption = Object.assign({}, option, {
        value: this.$refs.slider.value,
      });
      this.$dispatch('handleNextPage', path, newOption);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../utils/screen.scss";
@import "../../utils/color.scss";

.clothes-picker-page {
  min-width: 100vw;
  min-height: 100vh;
}

.container-fluid {
  margin-bottom: 2em;
}

.clothes-slider-box {
  margin-top: 1.5em;
}

.next-btn-box {
  display: flex;
  justify-content: center;
  margin-top: 46px;
}

.btn {
  max-width: 264px;
  width: 100%;
}

</style>



