import Vue from 'vue';
import App from './App';

Vue.mixin({
  screenSM: '48em',
  screenMD: '64em',
  screenLG: '75em',
});

import VueResource from 'vue-resource';
Vue.use(VueResource);

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: { App },
});



