<template>
  <div class="slider-box">
    <div class="slider">
      <div class="track">
        <div class="degree degree-left"></div>
        <div class="degree degree-right"></div>
      </div>
      <div class="inverse-left" :style="inverseLeftStyle">
        <div class="percentage">{{ calcLeftValue }}</div>
      </div>

      <div class="inverse-right" :style="inverseRightStyle">
        <div class="percentage">{{ calcRightValue }}</div>
      </div>

      <div class="range" :style="rangeStyle">
        <div class="degree degree-left"></div>
        <div class="degree degree-right"></div>
        <div class="percentage percentage-range">{{ calcCenterValue }}</div>
      </div>

      <span class="thumb thumb-left" :style="thumbLeftStyle"></span>
      <span class="thumb thumb-right" :style="thumbRightStyle"></span>
    </div>

    <input
      class="input-range"
      :style="leftInputStyle"
      type="range"
      tabindex="0"
      value="{{* left}}"
      max="100"
      min="0"
      step="1"
      v-model="left"
      @input="leftInput"
    />

    <input
      class="input-range"
      :style="rightInputStyle"
      type="range"
      tabindex="0"
      value="{{* right}}"
      max="100"
      min="0"
      step="1"
      v-model="right"
      @input="rightInput"
    />

    <input
      id="js-input"
      class="input-range input-range-track"
      type="range"
      tabindex="0"
      value="{{* trackValue}}"
      max="100"
      min="0"
      step="1"
      v-model="trackValue"
      @input="handleTrackInput"
    />
  </div>
</template>

<script>
import isIE from '../../../static/browserDetect';

export default {
  props: {
    colors: Array,
  },
  data() {
    return {
      left: 0,
      right: 0,
      trackValue: 0,
    };
  },
  computed: {
    calcLeftValue() {
      return `${this.left} %`;
    },
    calcRightValue() {
      return `${100 - this.right} %`;
    },
    calcCenterValue() {
      return `${this.right - this.left} %`;
    },
    inverseLeftStyle() {
      if (this.colors.length > 0) {
        return {
          width: `${this.left}%`,
          backgroundColor: this.colors[0].code,
        };
      }

      return {
        width: `${this.left}%`,
      };
    },
    inverseRightStyle() {
      if (this.colors.length <= 2) {
        return {
          display: 'none',
        };
      }

      let backgroundColor = '';
      if (this.colors[2]) {
        backgroundColor = this.colors[2].code;
      }

      return {
        width: `${100 - this.right}%`,
        backgroundColor,
      };
    },
    rangeStyle() {
      if (this.colors.length <= 1) {
        return {
          display: 'none',
        };
      }

      let backgroundColor = '';
      if (this.colors[1]) {
        backgroundColor = this.colors[1].code;
      }

      return {
        left: `${this.left}%`,
        right: `${100 - this.right}%`,
        backgroundColor,
      };
    },
    thumbLeftStyle() {
      if (isIE || this.colors.length === 1) {
        return {
          display: 'none',
        };
      }

      return {
        left: `${this.left}%`,
      };
    },
    thumbRightStyle() {
      if (!isIE && this.colors.length >= 3) {
        return {
          left: `${this.right}%`,
        };
      }

      return {
        display: 'none',
      };
    },
    leftInputStyle() {
      if (this.colors.length === 1) {
        return {
          display: 'none',
        };
      }

      return {};
    },
    rightInputStyle() {
      if (this.colors.length < 3) {
        return {
          display: 'none',
        };
      }

      return {};
    },
  },
  methods: {
    leftInput() {
      if (this.colors.length === 3) {
        this.left = Math.min(this.left, this.right - 1);
      }
    },
    rightInput() {
      this.right = Math.max(this.right, this.left + 1);
    },
    handleTrackInput() {
      const length = this.colors.length;
      if (length === 1) {
        this.trackValue = 100;
      } else if (length === 2) {
        this.left = this.trackValue;
      } else if (length === 3) {
        const center = this.left + (this.right - this.left) / 2;
        if (this.trackValue <= center) {
          this.left = this.trackValue;
        } else {
          this.right = this.trackValue;
        }
      }
    },
  },
  beforeCompile() {
    if (this.colors.length > 0) {
      if (this.colors.length === 1) {
        this.left = 100;
        this.right = 100;
      } else if (this.colors.length === 2) {
        this.left = this.colors[0].value;
        this.right = 100;
      } else if (this.colors.length === 3) {
        this.left = this.colors[0].value;
        this.right = this.colors[0].value + this.colors[1].value;
      }
    }

    this.$watch('colors', () => {
      const length = this.colors.length;
      if (length === 1) {
        this.left = 100;
        this.right = 100;
      } else if (length === 2) {
        if (this.right === 100) {
          this.left = this.left / 2;
        } else {
          this.right = 100;
        }
      } else if (length === 3) {
        this.right = this.left + (100 - this.left) / 2;
      }
    });

    this.$watch('trackValue', (value) => {
      const length = this.colors.length;
      if (length === 1) {
        this.trackValue = 100;
      } else if (length === 2) {
        this.left = value;
      } else if (length === 3) {
        const center = this.left + (this.right - this.left) / 2;
        if (value <= center) {
          this.left = value;
        } else {
          this.right = value;
        }
      }
    });
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

$default-color-track: #CCC;
$color-thumb: #e2e666;
$color-degree: #e2e666;
$color-percentage: #7fa1ae;

$thumb-side: 28px;

$track-height: 14px;
$track-margin: 0;

$degree-margin-top: 9px;
$degree-height: 14px;
$degree-width: 2px;

$percentage-margin-top: 35px;
$percentage-height: 17px;

.slider-box {
  position: relative;
  height: $track-height;
  text-align: left;
  margin-top: $thumb-side + $track-height;
  margin-bottom: $percentage-margin-top + $percentage-height;
}

.slider {
  position: absolute;
  left: $thumb-side / 2;
  right: $thumb-side / 2;
  height: $track-height;
}

.thumb {
  position: absolute;
  top: -($track-height + $thumb-side);
  z-index: 2;
  height: $thumb-side;
  width: $thumb-side;
  text-align: left;
  margin-left: -$thumb-side/2;
  cursor: pointer;
  background-color: $color-thumb;
  outline: none;
  border-radius: $thumb-side/2 $thumb-side/2 0 $thumb-side/2;
  transform:rotate(45deg);
}

.inverse-left {
  position: absolute;
  left: 0;
  height: $track-height;
  background-color: $default-color-track;
  margin: 0 $track-margin;
}

.range {
  position: absolute;
  left: 0;
  height: $track-height;
  background-color: $default-color-track;
}

.inverse-right {
  position: absolute;
  right: 0;
  height: $track-height;
  background-color: $default-color-track;
  margin: 0 $track-margin;
}

.degree {
  position: absolute;
  top: $track-height;
  width: $degree-width;
  height: $degree-height;
  background-color: $color-degree;
  margin-top: $degree-margin-top;
}

.degree-left {
  left: 0;
}

.degree-right {
  right: 0;
}

.percentage {
  position: absolute;
  left: 50%;
  top: $percentage-margin-top;
  min-width: 50px;
  height: $percentage-height;
  transform: translateX(-50%);
  font-family: Aleo;
	font-size: 15px;
	text-align: center;
  color: $color-percentage;
}

.percentage-range {
  left: 50%;
}

.input-range {
  position: absolute;
  pointer-events: none;
  -webkit-appearance: none;
  z-index: 3;
  height: $thumb-side;
  top: -($track-height + $thumb-side);
  width: 100%;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
}

.input-range::-ms-track {
  -webkit-appearance: none;
  background: transparent;
  color: transparent;
}

.input-range::-moz-range-track {
  -moz-appearance: none;
  background: transparent;
  color: transparent;
}

.input-range:focus::-webkit-slider-runnable-track {
  background: transparent;
  border: transparent;
}

.input-range:focus {
  outline: none;
}

.input-range::-ms-thumb {
  pointer-events: all;
  width: $thumb-side;
  height: $thumb-side;
  border-radius: 0;
  border: 0 none;
  cursor: pointer;
}

.input-range::-moz-range-thumb {
  pointer-events: all;
  width: $thumb-side;
  height: $thumb-side;
  border-radius: 0;
  border: 0 none;
  cursor: pointer;
}

.input-range::-webkit-slider-thumb {
  pointer-events: all;
  width: $thumb-side;
  height: $thumb-side;
  border-radius: 0;
  border: 0 none;
  -webkit-appearance: none;
  cursor: pointer;
}

.input-range::-ms-fill-lower {
  background: transparent;
  border: 0 none;
}

.input-range::-ms-fill-upper {
  background: transparent;
  border: 0 none;
}

.input-range::-ms-tooltip {
  display: none;
}

.input-range-track {
  top: -($track-height / 2);
  height: $track-height * 2;
  z-index: 3;
  cursor: pointer;
  pointer-events: auto;
}

</style>



